#Portfolio h1 {
    text-align: center;
    text-decoration: none;
}

/* more projects link */
#Portfolio .more_projects {
    margin-top: 20px;
    text-align: right;
}

#Portfolio .more_projects a {
    position: relative;
    text-decoration: none;
    cursor: pointer;
}

#Portfolio .more_projects a:after {
    position: absolute;
    bottom: -3px;
    left: 0;
    content: "";
    width: 0;
    height: 1px;
    transition: all 0.2s linear;
}

#Portfolio .more_projects a:hover::after {
    width: 100%;
}

/* light and dark mode */
.light_mode_portfolio {
    color: black;
    background: #87cefa;
}

.light_mode_portfolio h1 {
    color: white;
}

.light_mode_portfolio .card {
    border-color: black;
}

.light_mode_portfolio .more_projects a {
    color: white;
}

.light_mode_portfolio .more_projects a:hover::after {
    background-color: white;
}

.dark_mode_portfolio {
    color: white;
    background: #212121;
}

.dark_mode_portfolio h1 {
    color: white;
}

.dark_mode_portfolio .card {
    border-color: white;
    background-color: #c8c8c8;
}

.dark_mode_portfolio .more_projects a {
    color: white;
}

.dark_mode_portfolio .more_projects a:hover::after {
    background-color: white;
}
